.images{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 10px;
}
.images--image{
    position: relative;
    padding-top: 60%;
    top: 0;
    left: 0;
}
.images img{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
}
@media (max-width: 700px) {
    .images{
     grid-template-columns: 1fr;
        grid-gap: 10px;
    } 
}